
<script>
import { mapGetters, mapActions } from 'vuex'
import _game from "@/utils/api/game";

export default {
  name: 'SportStraight',
  data () {
    return {
      // 開放關閉開賽選擇
      selectItems: {
        selected: 0,
        options: [
          { text: '開放中＋關閉中', value: 0 },
          { text: '已開賽', value: 1 }
        ]
      },
      // 秒數倒數更新
      reciprocalItems: {
        selected: 180,
        options: [
          { text: '每180秒更新', value: 180 },
          { text: '每60秒更新', value: 60 },
          { text: '每5秒更新', value: 5 },
          { text: '不更新', value: '' }
        ],
        // 倒數 timer
        updateListTimer: null,
        // 當前秒數
        currentSpeed: '',
        // 確定更新秒數
        confirmSpeed: ''
      },
      // 拆帳選擇
      selectChangeItems: {
        selected: 1,
        options: [
          { text: '100％', value: 1 },
          { text: '自己', value: 0 }
        ]
      },
      // 玩法
      selectGameItems: {
        selected: 'N',
        options: [
          { text: '一般玩法', value: 'N' },
          { text: '多種玩法', value: 'Y' }
        ]
      },
      // 每頁顯示筆數
      selectRowsPerPageItems: {
        selected: 15,
        options: [
          { text: '15筆', value: 15 },
          { text: '30筆', value: 30 },
          { text: '60筆', value: 60 }
        ]
      },
      // 列表資料
      sportsList: {},
      pagination: {
        currentPage: 1, // 當前頁數
        rowsPerPage: 15, // 每頁筆數
        totalItems: 0 // 總筆數
      },
      // 頁面載入狀態
      loadingStatus: false,
      // 單式球類傳入參數
      gamelist: {
        act: 'normal',
        ball: '',
        time_interval: 'today',
        start: 0,
        occupy: 1,
        count_date: '',
        multi: '',
        page: 1,
        count: ''
      }
    }
  },

  computed: {
    ...mapGetters({
      getSportsState: 'sports/getSportsState'
      // 載入個人帳號
      // getProfileState: 'account-management/getProfileState'
    }),
    pages () {
      if (this.pagination.rowsPerPage == null || this.pagination.totalItems == null) return 0
      return Math.ceil(this.pagination.totalItems / this.pagination.rowsPerPage)
    }
  },
  watch: {
    getSportsState (payload) {
      // 調整獨立聯盟名稱位置而重組資料
      let list = {};
      // console.log('單式 data_list: ', payload.result.data_list);
      (payload.result.data_list).forEach(el => {
        let l_name = `${el.league_name}${el.transType}`
        if(!list[l_name]) list[l_name] = [] ;
        list[l_name].push(el)
      });
      this.sportsList = list
      // console.log('單式 sportsList: ', list);

      // 若 API 有當前頁數回傳資料，可設定如下
      this.pagination.currentPage = payload.result.current_page
      this.pagination.totalItems = payload.result.total_count
      // this.sportsList.contents = payload.result.list // ?不清楚用處?
    },
    'pagination.rowsPerPage' (rowsPerPage) {
      // 回第一頁
      this.pagination.currentPage = 1
      // this.loadSports()
    },
    // (每頁顯示)切換
    'selectRowsPerPageItems.selected' (value) {
      // 回第一頁
      this.gamelist.page = 1
      this.pagination.currentPage = 1

      this.gamelist.count = value
      this.loadSports()
    },
    // (開放中＋關閉中)切換
    'selectItems.selected' (value) {
      // 回第一頁
      this.gamelist.page = 1
      this.pagination.currentPage = 1

      this.gamelist.start = value
      this.loadSports()
    },
    // 每秒更新切換
    'reciprocalItems.selected' (value) {
      this.updateTableList()
    },
    'reciprocalItems.currentSpeed' (value) {
      // 倒數為0秒時刷新列表
      if (value === 0) {
        this.loadSports()
      }
    },
    // (拆帳)切換
    'selectChangeItems.selected' (value) {
      // 回第一頁
      this.gamelist.page = 1
      this.pagination.currentPage = 1

      this.gamelist.occupy = value
      this.loadSports()
    },
    // (玩法)切換
    'selectGameItems.selected' (value) {
      // 回第一頁
      this.gamelist.page = 1
      this.pagination.currentPage = 1

      // this.gamelist.multi = value
      // 顯示切換玩法資料表
      if (value === 'N') {
        if (this.tabledata1 !== undefined) this.tabledata1.show = true
        if (this.tabledata2 !== undefined) this.tabledata2.show = false
        // 依照選擇玩法搜尋表格
        this.loadSports()
        // 載入狀態(開始)
        this.loadingStatus = true
      } else if (value === 'Y') {
        if (this.tabledata1 !== undefined) this.tabledata2.show = true
        if (this.tabledata2 !== undefined) this.tabledata1.show = false
        this.loadSports()
      }
    },
    getProfileState (payload) {
      this.gamelist.account = payload.result.l_no
    }
  },
  mounted () {
    this.updateTableList()

    // 顯示全部玩法資料表
    if (this.selectGameItems.selected === 'N') {
      if (this.tabledata1 !== undefined) this.tabledata1.show = true
      if (this.tabledata2 !== undefined) this.tabledata2.show = false
      // 依照選擇玩法搜尋表格
      // 載入狀態(開始)
      this.loadingStatus = true
    }
  },
  destroyed () {
    // 清除 timer
    clearInterval(this.reciprocalItems.updateListTimer)
  },
  methods: {
    ...mapActions({
      getSports: 'api/getSports',
      // getYasin: 'api/getYasin'
    }),
    // 載入單式資料
    async loadSports (page, rowsPerPage) {
      // 載入狀態(開始)
      this.loadingStatus = true
      let params = {
        act: this.gamelist.act,
        ball: this.gamelist.ball,
        multi: this.selectGameItems.selected,
        time_interval: this.gamelist.time_interval,
        start: this.gamelist.start ? this.gamelist.start : this.selectItems.selected,
        occupy: this.gamelist.occupy ? this.gamelist.occupy : this.selectChangeItems.selected,
        count_date: this.gamelist.count_date,
        page: page !== undefined ? page : this.pagination.currentPage,
        count: this.pagination.rowsPerPage = this.gamelist.count ? this.gamelist.count : this.selectRowsPerPageItems.selected
      }
      console.log('getSports', params)
      await _game.getSports(this.$store, params)
      console.log(33)
      this.loadingStatus = false
      // this.getSports(params)
      //   .then(res => {
      //     if (res.status === 200) {
      //       // 載入狀態(結束)
      //       this.loadingStatus = false
      //     }
      //   })
      //   .catch(err => {
      //     console.log(err)
      //   })
    },
    // 載入下一頁
    loadNextPage (page) {
      // 若 API 有當前頁數回傳資料，可註解以下
      this.pagination.currentPage = page
      this.loadSports()
    },
    /**
     * 秒數倒數
     * @params  [speed]  倒數秒數
     **/
    startCountdown (speed) {
      this.reciprocalItems.currentSpeed = speed
      this.reciprocalItems.updateListTimer = setInterval(() => {
        this.reciprocalItems.currentSpeed--
        // 秒數為負數，則重新倒數
        if (this.reciprocalItems.currentSpeed < 0) {
          // 清除 timer
          clearInterval(this.reciprocalItems.updateListTimer)
          // 重新倒數秒數
          this.startCountdown(this.reciprocalItems.confirmSpeed)
        }
      }, 1000)
    },
    // 啟動秒數倒數刷新表格資料
    updateTableList () {
      this.loadSports()

      // 確定更新秒數
      this.reciprocalItems.confirmSpeed = this.reciprocalItems.selected

      // 清除 timer
      clearInterval(this.reciprocalItems.updateListTimer)

      // 秒數為負數，則不更新倒數功能
      if (this.reciprocalItems.selected < 0) {
        // 當前秒數設置空值
        this.reciprocalItems.currentSpeed = ''
        return
      }
      // 倒數秒數
      this.startCountdown(this.reciprocalItems.confirmSpeed)
    }
  }
}
</script>
